export function setMapValue(map, keys, value){
	var _map = map 
	for (var i=0; i< keys.length; i++){
		var key = keys[i]
		if (i === keys.length - 1){
			_map.set(key, value)
			return
		}

		if (!_map.has(key)){
			_map.set(key, new Map())
		}
		_map = _map.get(key)
	}
}


export default {
	quality_data,
	setMapValue
}